<template>
  <div class="preferences-page">
    <v-toolbar dense :dark="true">
      <v-app-bar-nav-icon @click="goToHome"></v-app-bar-nav-icon>

      <v-spacer></v-spacer>
      <v-toolbar-title>Preferences</v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- <v-icon>mdi-people</v-icon> -->
    </v-toolbar>

    <v-main>
      <v-row class="row pa-3 align-center">
        <v-col cols="5">
          <p class="text white--text">Currency</p>
        </v-col>
        <v-col cols="6">
          <v-select
            dense
            flat
            hide-details
            hide-selected
            dark
            :append-icon="null"
            color="white"
            v-model="currency"
            :items="currencies"
            @change="changeCurrency"
          />
        </v-col>

        <v-col cols="5">
          <p class="text white--text">Theme</p>
        </v-col>
        <v-col cols="6">
          <v-select
            dense
            flat
            hide-details
            hide-selected
            dark
            :append-icon="null"
            color="white"
            v-model="isDark"
            :items="themes"
            @change="changeTheme"
          />
        </v-col>
      </v-row>
    </v-main>
  </div>
</template>

<script>
export default {
  name: "Preferences",

  data: () => {
    return {
      language: null,
      languages: [{ text: "US", value: "us" }],

      isDark: false,
      themes: [
        { text: "Light", value: false },
        { text: "Dark", value: true }
      ],

      currency: "US Dollar ($)"
    };
  },

  methods: {
    goToHome() {
      this.$router.replace({ name: "Home" });
    },

    changeCurrency(selected) {
      const currency = this.currencies.find(c => {
        return c.value == selected;
      });
      this.$store.commit("currency", currency);
    },

    changeTheme(isDark) {
      this.$store.commit("theme", isDark);
    }
  },

  beforeDestroy() {
    this.$store.commit("updatePreferences");
  },

  created() {
    const preferences = this.$store.getters["preferences"];
    this.currency = preferences.currency.value;
    this.isDark = preferences.darkMode;

    this.currencies = this.$store.getters["currencyList"];
  }
};
</script>
